@media screen and (max-width: 767px) {
  .backdrop {
    width: 100%;
    height: 100%;
    z-index: 2000;
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    background: rgba(0, 0, 0, .2);
    transition: opacity;
    position: fixed;
    top: 0;
    left: 0;
  }

  .backdrop.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  .modal-header {
    width: 280px;
    height: 990px;
    max-height: 100%;
    background: #fcfcfc;
    border: 2px solid #f8d24e;
    border-radius: 10px;
    padding: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    overflow-y: auto;
    transform: translate(-50%, -50%);
  }

  .modal-header-close-btn {
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    justify-content: center;
    align-items: center;
    padding: 0;
    display: flex;
    position: absolute;
    top: 8px;
    right: 8px;
  }

  .header-name-form {
    text-align: center;
    color: #131313;
    align-items: center;
    margin-bottom: 20px;
    font-family: Seymour One;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }

  .order {
    align-items: center;
    margin-bottom: 24px;
    display: block;
  }

  .order-list-cream__order {
    width: 132px;
    height: 200px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 73px;
    position: relative;
  }

  .order-list-cream__order:before {
    height: 132px;
    width: 132px;
    content: "";
    z-index: 100;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7)), #d9d9d9;
    border-radius: 50%;
    display: block;
    position: absolute;
    top: 73px;
    left: 0;
    overflow: hidden;
    transform: translateY(-60px);
    box-shadow: 0 7px rgba(156, 190, 143, .7);
  }

  .card-cream:before {
    height: 132px;
    width: 132px;
    background-image: url("ice-cream-mobile.b3fb4954.jpg");
  }
}

